body{
  margin: 0;
}
.App {
  text-align: center;
  margin: 0%;
  padding: 3rem;
  box-sizing: border-box;
  background: rgb(2,0,36);
  background: linear-gradient(180deg, rgba(2,0,36,1) 0%, rgba(28,9,121,0.8911939775910365) 54%, rgba(0,212,255,1) 100%);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

